import { Component, Element, Event, EventEmitter, Host, Listen, Prop, State, Watch, h } from '@stencil/core';

const isHTMLMdxFlyoutItemElement = (element: string | HTMLMdxFlyoutItemElement): element is HTMLMdxFlyoutItemElement => {
  if (typeof element === 'object' && typeof element.item === 'string') {
    return true;
  }

  return false;
};

@Component({
  tag: 'mdx-select',
  styleUrl: 'select.scss',
  shadow: true,
})
export class Select {
  @Element()
  host: HTMLMdxSelectElement;

  @Event()
  selectChange: EventEmitter<HTMLMdxFlyoutItemElement['item']>;

  @Prop({ reflect: true })
  selectedItem: HTMLMdxFlyoutItemElement['item'];

  @Prop()
  disabled = false;

  @Prop()
  required = false;

  @Prop()
  label!: string;

  @Prop()
  text!: string;

  @Prop()
  hint?: string;

  @Prop()
  error?: string;

  @Prop({ reflect: true })
  open = false;

  @State()
  focused = false;

  @State()
  keyboardEvent = false;

  @Listen('click', { capture: true, target: 'window' })
  handleClick() {
    this.keyboardEvent = false;
  }

  @Listen('keydown', { capture: true, target: 'window' })
  handleKeyDown() {
    this.keyboardEvent = true;
  }

  @Listen('selectButtonBlur', { capture: true })
  onButtonBlur() {
    this.focused = false;
  }

  @Listen('selectButtonFocus', { capture: true })
  onButtonFocus() {
    this.keyboardEvent && (this.focused = true);
  }

  @Listen('selectButtonClick', { capture: true })
  onButtonClick() {
    this.open = !this.open;
  }

  @Watch('selectedItem')
  onSelectChange() {
    this.selectChange.emit(this.selectedItem);
  }

  private select(item: string) {
    if (this.selectedItem === item) {
      return false;
    }
    this.selectedItem = item;
    return true;
  }

  private get items() {
    return Array.from(this.host.querySelectorAll('mdx-flyout-item'));
  }

  private getItems(item: string | HTMLMdxFlyoutItemElement): HTMLMdxFlyoutItemElement | undefined {
    return isHTMLMdxFlyoutItemElement(item) ? item : this.items.find((t) => t.item === item);
  }

  componentWillUpdate() {
    const selectedItem = this.getItems(this.selectedItem);
    if (selectedItem) {
      selectedItem.selected = true;
    }
  }

  render() {
    const itemLabel = this.getItems(this.selectedItem)?.label;
    return (
      <Host
        onItemClick={({ detail: { item } }) => {
          this.select(item);
        }}
      >
        <mdx-select-button
          disabled={this.disabled}
          required={this.required}
          label={this.label}
          placeholder={!itemLabel}
          open={this.open}
          text={itemLabel || this.text}
        >
          <div class="select__information-wrapper">
            <div>
              {this.hint && (typeof this.error === 'undefined' || this.error === '') && (
                <span class="select__hint">{this.hint}</span>
              )}
              {this.error && <mdx-error-hint text={this.error} />}
            </div>
            <div class="select__flyout" aria-haspopup="true" aria-expanded={this.open ? 'true' : 'false'}>
              <slot></slot>
            </div>
          </div>
        </mdx-select-button>
      </Host>
    );
  }
}
