:host .select {
  &__flyout {
    position: absolute;
    width: 100%;
    max-height: 0;
    overflow: hidden;
    transition: var(--mdx-comp-flyout-transition-expand-collapse);
  }

  &__information-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font: var(--mdx-comp-select-font-supporting);
    position: relative;
  }

  &__hint {
    color: var(--mdx-comp-select-hint-counter-color-default);
  }

  &.focused {
    border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
    color: var(--mdx-comp-select-label-color-focus);
    outline-color: var(--mdx-comp-keyboard-focus-color-default);
    outline-style: solid;
    outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
    outline-offset: 2px;
  }

  &:focus-within &__information-wrapper &__hint,
  &:focus-within &__information-wrapper &__error-message {
    color: var(--mdx-comp-select-hint-counter-color-focus);
  }
}


:host([open]) .select {
  &__flyout {
    max-height: 100vh;
    box-shadow: var(--mdx-comp-flyout-box-shadow-default);
    transition: var(--mdx-comp-flyout-transition-expand-collapse);
  }
}
